import React from 'react';

import classes from './productPreview.module.css'


const ProductPreview = (props)=>{
    const currentHour = new Date().getHours() > 9? new Date().getHours(): '0' + new Date().getHours();
    const currentMinutes = new Date().getMinutes() > 9? new Date().getMinutes(): '0' + new Date().getMinutes();
    
    return(
        <div className={classes.previewProduct}>
            <img src={props.currentPreviewImage} alt='previewproduct'/>

        {
            props.currentSelectedFeature === 1?
            <div className={classes.hbDisplay}>
                <i className="fas fa-heartbeat"></i>
                <p>78</p>
            </div>
            :
            <div className={classes.watchDisplay}>
                <p>{`${currentHour}:${currentMinutes}`}</p>
            </div>
        }
            

            
        </div>
    )
}

export default ProductPreview;