import React, {Component} from 'react';
import classes from './App.module.css';
import ProductDescription from './ProductDescription/ProductDescription';
import ProductPreview from './productPreview/productPreview';
import Topbar from './Topbar/topBar';
import ProductData from './ProductData';

class App extends Component {
  state = {
    productData: ProductData,
    currentPreviewImagePos:0,
    currentSelectedFeature: 0,
  }
  
  onColorOptionClick = (pos) => {
    this.setState({currentPreviewImagePos: pos});
  }

  onFeaturesClick = (pos) =>{
    this.setState({currentSelectedFeature: pos})
  }

  render(){
    
    return (
      <div className="App">
        <Topbar/>
      <div className={classes.main}>
  
        <div className={classes.previewProduct}>
          <ProductPreview currentPreviewImage ={this.state.productData.colorOptions[this.state.currentPreviewImagePos].imageUrl} 
          currentSelectedFeature={this.state.currentSelectedFeature}/>
        </div>
  
        <div className={classes.descriptionSection}>
         <ProductDescription data={this.state.productData} 
         onFeaturesClick={this.onFeaturesClick} 
         onColorOptionClick= {this.onColorOptionClick} 
         currentSelectedFeature={this.state.currentSelectedFeature}/>
        </div>
      </div>
      </div>
    )

  }

}

export default App;
