import React from 'react';

import classes from './ProductDescription.module.css';



const ProductDescription = (props) =>{
    const colorOptions = props.data.colorOptions.map((item, pos)=>{
        const classArr = [classes.watchImage];

        if(pos === props.currentPreviewImagePos){
            classArr.push(classes.watchImageSelected)
        };

        return(
            <img key={pos} className={classArr.join('  ')} src={item.imageUrl} alt={item.styleName} onClick={()=>props.onColorOptionClick(pos)}/>
        );
    })

    const featureList = props.data.featureList.map((item, pos)=>{
        const classArr = [classes.featureItem];

        if(pos === props.currentSelectedFeature){
            classArr.push(classes.featureItemSelected);
        }
        return(
            <button key={pos} className={classArr.join(' ')} onClick={()=> props.onFeaturesClick(pos)}>{item}</button>
        )
    })
    return(
        <div className={classes.descriptionSection}>
            <h1 className={classes.productName}>
                {props.data.title}
            </h1>
            <p className={classes.productDescription}>
                {props.data.description}
            </p>

            <h3 className={classes.colourOptions}>Select Colour</h3>
            <div className={classes.watchesOptions}>
                {colorOptions}
            </div>

            <h3 className={classes.colourOptions}>Features</h3>
            <div>
                {featureList}
            </div>

            <button className={classes.primaryButton}>Buy Now</button>
        </div>
    )
}

export default ProductDescription;